import React from "react"

// Rendering individual images
const Image = ({ image }) => {
  return (
    <div className="w-1/4 border-gray-100 rounded-lg p-1 ">
      <div
        className="square rounded-lg"
        style={{
          backgroundImage: "url(" + image.data + ")",
          backgroundSize: "cover",
        }}
      >
        <div className="p-4">{image.name}</div>
      </div>
      {/* <img alt={`img - ${image.id}`} src={image.src} className="file-img" /> */}
      {/* {console.log("IMG: ", img)} */}
    </div>
  )
}

// ImageList Component
const ImageList = ({ images }) => {
  // render each image by calling Image component
  const renderImage = (image, index) => {
    return <Image image={image} key={`${image.id}-image`} />
  }

  // Return the list of files
  return <div className="flex flex-wrap mt-2">{images.map(renderImage)}</div>
}

export default ImageList
