import React, { useCallback, useState } from "react"
// import { withPrefix } from "gatsby"
// import SmtpService from "../components/SmtpService"
// import FileLoader from "../components/FileLoader"
import SEO from "../components/seo"
import { useTranslation } from "react-i18next"
import Dropzone from "../components/dropzone"
import ImageList from "../components/imageList"
import cuid from "cuid"

import Helmet from "react-helmet"

const ContactsPage = ({ ...props }) => {
  const { t } = useTranslation()
  const onDrop = useCallback(acceptedFiles => {
    // this callback will be called after files get dropped, we will get the acceptedFiles. If you want, you can even access the rejected files too
    console.log("acceptedFiles: ", acceptedFiles)
    // Loop through accepted files
    acceptedFiles.map(file => {
      // Initialize FileReader browser API
      const reader = new FileReader()
      // onload callback gets called after the reader reads the file data
      reader.onload = function(e) {
        // add the image into the state. Since FileReader reading process is asynchronous, its better to get the latest snapshot state (i.e., prevState) and update it.
        console.log("event: ", file.name)

        setImages(prevState => [
          ...prevState,
          { id: cuid(), data: e.target.result, name: file.name },
        ])
      }
      // Read the file as Data URL (since we accept only images)
      reader.readAsDataURL(file)
      return file
    })
  }, [])
  const [images, setImages] = useState([])
  const [senderAddress, setSenderAddress] = useState("example@example.com")
  const [senderName, setSenderName] = useState("Your Name")
  const [senderSubj, setSenderSubj] = useState("Subject")
  const [senderMsg, setSenderMsg] = useState("Your message")

  const sendMail = () => {
    window.Email.send({
      SecureToken: "e40fa2d4-483f-402b-9b9e-81e81a4a327e",
      To: "obiwan@mail.ru",
      From: "admin@alive-life.com",
      ReplyAddress: senderAddress,
      Subject: senderSubj,
      Body:
        "From: " +
        senderName +
        "<br>Email:" +
        senderAddress +
        "<br>Subject:" +
        senderSubj +
        "<br>Message:" +
        senderMsg,
      Attachments: images,
    }).then(message => alert(message))
  }

  return (
    <div className="mx-auto text-gray-100">
      <SEO title={t("contacts.title")} />

      <Helmet>
        <script
          // src={"https://smtpjs.com/v3/smtp.js"}
          src={"/smtp.js"}
          type="text/javascript"
        />
      </Helmet>

      <div className="w-full px-0 lg:px-40">
        <h1 className="text-2xl text-gray-400 my-4 w-full text-center">
          {t("contacts.title")}
        </h1>
        <h2 className="text-2xl text-gray-400 my-4 w-full text-center">
          {t("contacts.services")}
        </h2>
        {/* --Contact form -- */}
        <div className="flex flex-col md:px-4 w-full">
          <div className="flex flex-col sm:flex-row">
            <div className="flex-col w-full sm:w-1/2 p-4">
              {/* Left side container */}
              <div>
                {/*-- Credentials box -*/}
                <div className="w-full">
                  <div className="uppercase text-gray-100 px-4">
                    {t("contacts.name")}
                  </div>
                  <input
                    value={senderName}
                    onChange={e => setSenderName(e.target.value)}
                    type="text"
                    name="name"
                    className="w-full bg-gray-700 rounded-lg px-4 py-2"
                  />
                </div>
                <div className="w-full">
                  <div className="uppercase text-gray-100 px-4">
                    {t("contacts.email")}
                  </div>
                  <input
                    value={senderAddress}
                    onChange={e => setSenderAddress(e.target.value)}
                    type="email"
                    name="email"
                    className="w-full bg-gray-700 rounded-lg px-4 py-2"
                  />
                </div>
                <div className="w-full">
                  <div className="uppercase text-gray-100 px-4">
                    {t("contacts.subject")}
                  </div>
                  <input
                    value={senderSubj}
                    onChange={e => setSenderSubj(e.target.value)}
                    type="text"
                    name="subject"
                    className="w-full bg-gray-700 rounded-lg px-4 py-2"
                  />
                </div>
              </div>
            </div>
            <div className="sm:w-1/2 p-4">
              {/*<!-- Right side container -->*/}
              <div className="flex flex-col h-full">
                {/*<!-- Message box -->*/}
                <label type="text" className="uppercase text-gray-100 px-4">
                  {t("contacts.message")}
                </label>
                <div className="flex-grow">
                  <div className="flex-1 flex-grow h-32 sm:h-full">
                    <textarea
                      value={senderMsg}
                      onChange={e => setSenderMsg(e.target.value)}
                      className="resize-none h-full w-full bg-gray-700 border-gray-100 rounded-lg text-gray-100 px-4"
                    ></textarea>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* <!-- Bottom images --> */}
        <div className="flex flex-col md:px-8 w-full">
          <Dropzone onDrop={onDrop} accept={"image/*"} />
          <ImageList images={images} />
          <button
            className="text-gray-100 bg-gray-700 rounded-lg md:p-8 w-full my-4"
            onClick={sendMail}
          >
            {t("contacts.submit")}
          </button>
        </div>
      </div>
    </div>
  )
}
export default ContactsPage
